@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
:root {
  --text-color-1: #ffffff;
  --text-color-2: #000000;
  --element-color: #FFC7C7;
  --element-background: #434C7B;
}

body {
  font-family: "Open Sans", serif;
  background: -webkit-gradient(linear, left top, right bottom, from(#434C7B), to(#FFC7C7));
  background: linear-gradient(to bottom right, #434C7B, #FFC7C7);
  background: -webkit-gradient(linear, left top, right bottom, from(var(--element-background)), to(var(--element-color)));
  background: linear-gradient(to bottom right, var(--element-background), var(--element-color));
}

.container {
  padding: 0px;
  margin-top: 5vh;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 5vh;
}

.row {
  text-align: center;
  margin: auto;
  padding: auto;
  box-shadow: 15px 5px 15px rgba(0, 0, 0, 0.75);
  background-color: #434C7B;
  background-color: var(--element-background);
}

.title-container {
  position: relative;
  background: url(//ice1224.github.io/weather-app-react/static/media/bckg.82b686d7.png) 54% no-repeat;
  background-size: cover;
  display: flex;
  padding: 10%;
  justify-content: center;
  text-align: center;
  color: #000000;
  color: var(--text-color-2);
  min-height: 90vh;
}

.title-container__title {
  font-size: 50px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: 'Roboto Slab', serif;
}

.title-container__subtitle {
  font-style: italic;
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: "Merriweather", serif;
}

.scroll_down_button {
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 2px solid #ffffff;
  border: 2px solid var(--text-color-1);
  text-align: center;
  padding: 0px;
  font-size: 1.4em;
  left: 0;
  right: 0;
  bottom: 10%;
  background-color: transparent;
  color: #ffffff;
  color: var(--text-color-1);
  outline: none;
}

.form-container {
  padding-top: 80px;
  min-height: 90vh;
  background-color: #434C7B;
  background-color: var(--element-background);
  padding-bottom: 5vh;
}

.form-container form {
  text-align: center;
}

input[type="text"] {
  background-color: transparent;
  min-width: 250px;
  border: 0;
  border-bottom: solid 1px #FFC7C7;
  border-bottom: solid 1px var(--element-color);
  width: 30%;
  padding-bottom: 4px;
  color: #ffffff;
  color: var(--text-color-1);
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 20px;
  outline: none;
}

::-webkit-input-placeholder {
  color: #ffffff;
  color: var(--text-color-1);
  opacity: 0.6;
}

::-moz-placeholder {
  color: #ffffff;
  color: var(--text-color-1);
  opacity: 0.6;
}

:-ms-input-placeholder {
  color: #ffffff;
  color: var(--text-color-1);
  opacity: 0.6;
}

::-ms-input-placeholder {
  color: #ffffff;
  color: var(--text-color-1);
  opacity: 0.6;
}

::placeholder {
  color: #ffffff;
  color: var(--text-color-1);
  opacity: 0.6;
}

input:-webkit-autofill {
  box-shadow: 0 0 0 30px #434C7B inset;
  box-shadow: 0 0 0 30px var(--element-background) inset;
  -webkit-box-shadow: 0 0 0 30px #434C7B inset;
  -webkit-box-shadow: 0 0 0 30px var(--element-background) inset;
  -webkit-text-fill-color: #ffffff !important;
  -webkit-text-fill-color: var(--text-color-1) !important;
}

button {
  display: block;
  border: 2px solid #FFC7C7;
  border: 2px solid var(--element-color);
  padding: 8px 20px;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #FFC7C7;
  background-color: var(--element-color);
  color: #000000;
  color: var(--text-color-2);
  font-weight: 400;
  outline: none;
}

button:active {
  outline: none;
  color: #FFC7C7;
  color: var(--element-color);
  background-color: #434C7B;
  background-color: var(--element-background);
  border: 2px solid #FFC7C7;
  border: 2px solid var(--element-color);
}

button:focus {
  outline: none;
}

.weather__info {
  width: 100%;
  padding-left: 10%;
  padding-right: 10%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
  text-align: left;
}

.weather__key {
  color: #FFC7C7;
  color: var(--element-color);
  border-bottom: solid 2px rgba(255, 255, 255, 0.06);
  padding: 20px 0 20px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: #ffffff;
  color: var(--text-color-1);
  font-weight: 200;
}

.weather__error {
  color: #FFC7C7;
  color: var(--element-color);
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}
